import { Component, OnInit } from '@angular/core';
import { IAlbum, Lightbox } from 'ngx-lightbox';

@Component({
  selector: 'app-selectos-medina',
  templateUrl: './selectos-medina.component.html',
  styleUrls: ['./selectos-medina.component.css']
})

export class SelectosMedinaComponent implements OnInit {

  constructor(private _lightbox: Lightbox) { }

  ngOnInit(): void {
  }

  open(album: IAlbum[]): void {
    this._lightbox.open(album, 0);
  }
}
