import { Component, OnInit } from '@angular/core';
import { IAlbum, Lightbox } from 'ngx-lightbox';

@Component({
  selector: 'app-ayudtlax',
  templateUrl: './ayudtlax.component.html',
  styleUrls: ['./ayudtlax.component.css']
})

export class AyudtlaxComponent implements OnInit {

  constructor(private _lightbox: Lightbox) { }

  ngOnInit(): void {
  }

  open(album: IAlbum[]): void {
    this._lightbox.open(album, 0);
  }
}
