import { Component, OnInit } from '@angular/core';
import { IAlbum, Lightbox } from 'ngx-lightbox';

@Component({
  selector: 'app-sebn',
  templateUrl: './sebn.component.html',
  styleUrls: ['./sebn.component.css']
})

export class SebnComponent implements OnInit {

  constructor(private _lightbox: Lightbox) { }

  ngOnInit(): void {
  }

  open(album: IAlbum[]): void {
    this._lightbox.open(album, 0);
  }
}
